"use client";

import { useState, useRef, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { ScrollArea } from "@/components/ui/scroll-area";
import Link from "next/link";

interface Message {
  id: string;
  text: string;
  sender: "user" | "bot";
  timestamp: Date;
}

const NATUROPATA_RESPONSES = {
  greeting: [
    "Olá! Sou Dr. Silva, Naturopata Sênior especializado em biorressonância. Como posso ajudá-lo hoje?",
    "Bem-vindo! Tenho mais de 20 anos de experiência em medicina integrativa e biorressonância. Em que posso auxiliá-lo?",
    "Oi! Sou especialista em biorressonância e tratamentos com nanotecnologia. Como posso orientá-lo?"
  ],
  
  bioresonancia: [
    "A biorressonância é uma técnica revolucionária que analisa as frequências eletromagnéticas do seu corpo. Cada célula, órgão e sistema possui uma frequência específica. Quando há desequilíbrios, essas frequências se alteram, e podemos detectar e corrigir essas alterações.",
    "Imagine seu corpo como uma orquestra - cada órgão é um instrumento que deve tocar em harmonia. A biorressonância identifica quais 'instrumentos' estão desafinados e os ajuda a retornar ao tom correto através de frequências terapêuticas específicas.",
    "É um método não-invasivo que utiliza as próprias frequências do corpo para promover o equilíbrio e a cura natural. Detectamos padrões energéticos alterados e aplicamos frequências corretivas personalizadas."
  ],
  
  nanotecnologia: [
    "Nossa nanotecnologia trabalha em escala molecular, permitindo que os tratamentos atuem diretamente nas células afetadas. Utilizamos nanopartículas programadas para carregar frequências terapêuticas específicas até o local exato que precisa de tratamento.",
    "A nanotecnologia potencializa os efeitos da biorressonância. Criamos 'veículos moleculares' que transportam as frequências curativas diretamente para as células, aumentando a eficácia do tratamento em até 300%.",
    "É como ter pequenos 'médicos microscópicos' trabalhando dentro do seu corpo, levando a cura exatamente onde ela é necessária, sem afetar tecidos saudáveis."
  ],
  
  beneficios: [
    "Os benefícios incluem: melhora do sistema imunológico, redução do estresse, equilíbrio hormonal, desintoxicação celular, melhora da qualidade do sono, aumento da energia vital e prevenção de doenças.",
    "Nossos pacientes relatam: diminuição de dores crônicas, melhora da digestão, maior clareza mental, redução da ansiedade, fortalecimento do sistema nervoso e regeneração celular acelerada.",
    "Além da cura, promovemos o bem-estar integral: harmonização dos chakras, equilíbrio emocional, revitalização energética e otimização das funções orgânicas."
  ],
  
  preparo: [
    "O preparo é simples: venha com roupas confortáveis, evite bebidas alcoólicas 24h antes, mantenha-se hidratado e, se possível, faça uma alimentação leve no dia do exame. O mais importante é vir com mente aberta e receptiva.",
    "Recomendo jejum de 2 horas antes do exame, evitar medicamentos não essenciais (consulte seu médico), usar roupas de algodão e remover objetos metálicos. Traga uma lista de seus sintomas e histórico de saúde.",
    "Prepare-se mentalmente para uma experiência transformadora. Medite alguns minutos antes de vir, evite estresse no dia do exame e traga expectativas positivas. Seu estado emocional influencia os resultados."
  ],
  
  agendamento: [
    "Vou ajudá-lo a agendar seu exame! Preciso de algumas informações: seu nome completo, telefone, cidade onde mora e sua disponibilidade de datas. Temos pontos de atendimento em várias regiões.",
    "Para agendar, preciso saber: qual região você prefere para o atendimento, se tem alguma preferência de horário (manhã ou tarde), e se há alguma urgência específica no seu caso.",
    "Ótimo! Vamos encontrar o melhor horário para você. Me informe sua cidade, disponibilidade de dias da semana e se prefere manhã ou tarde. Nossos pontos de atendimento têm horários flexíveis."
  ],
  
  default: [
    "Entendo sua preocupação. Como naturopata, sempre busco a abordagem mais natural e eficaz. Pode me dar mais detalhes sobre sua situação específica?",
    "Essa é uma excelente pergunta! Na medicina integrativa, consideramos sempre o ser humano como um todo. Me conte mais sobre seus sintomas ou objetivos.",
    "Com minha experiência, posso afirmar que cada caso é único. Para dar a melhor orientação, preciso entender melhor sua situação. Pode compartilhar mais detalhes?"
  ]
};

export default function ChatbotPage() {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: "1",
      text: "Olá! Sou Dr. Silva, Naturopata Sênior especializado em biorressonância com mais de 20 anos de experiência. Estou aqui para esclarecer suas dúvidas sobre exames de biorressonância, tratamentos com nanotecnologia e ajudá-lo a agendar seu exame. Como posso ajudá-lo hoje?",
      sender: "bot",
      timestamp: new Date()
    }
  ]);
  const [inputMessage, setInputMessage] = useState("");
  const [isTyping, setIsTyping] = useState(false);
  const scrollAreaRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    if (scrollAreaRef.current) {
      const scrollContainer = scrollAreaRef.current.querySelector('[data-radix-scroll-area-viewport]');
      if (scrollContainer) {
        scrollContainer.scrollTop = scrollContainer.scrollHeight;
      }
    }
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const getResponse = (userMessage: string): string => {
    const message = userMessage.toLowerCase();
    
    if (message.includes("olá") || message.includes("oi") || message.includes("bom dia") || message.includes("boa tarde")) {
      return NATUROPATA_RESPONSES.greeting[Math.floor(Math.random() * NATUROPATA_RESPONSES.greeting.length)];
    }
    
    if (message.includes("biorressonância") || message.includes("bioressonancia") || message.includes("exame") || message.includes("o que é")) {
      return NATUROPATA_RESPONSES.bioresonancia[Math.floor(Math.random() * NATUROPATA_RESPONSES.bioresonancia.length)];
    }
    
    if (message.includes("nanotecnologia") || message.includes("nano") || message.includes("molecular")) {
      return NATUROPATA_RESPONSES.nanotecnologia[Math.floor(Math.random() * NATUROPATA_RESPONSES.nanotecnologia.length)];
    }
    
    if (message.includes("benefício") || message.includes("vantagem") || message.includes("resultado") || message.includes("melhora")) {
      return NATUROPATA_RESPONSES.beneficios[Math.floor(Math.random() * NATUROPATA_RESPONSES.beneficios.length)];
    }
    
    if (message.includes("preparo") || message.includes("preparar") || message.includes("antes do exame") || message.includes("jejum")) {
      return NATUROPATA_RESPONSES.preparo[Math.floor(Math.random() * NATUROPATA_RESPONSES.preparo.length)];
    }
    
    if (message.includes("agendar") || message.includes("agendamento") || message.includes("marcar") || message.includes("consulta")) {
      return NATUROPATA_RESPONSES.agendamento[Math.floor(Math.random() * NATUROPATA_RESPONSES.agendamento.length)] + 
             "\n\nVocê também pode agendar diretamente através do nosso sistema online. Gostaria que eu te direcionasse para lá?";
    }
    
    if (message.includes("preço") || message.includes("valor") || message.includes("custo") || message.includes("quanto custa")) {
      return "Os valores variam conforme o tipo de exame e tratamento necessário. Em uma consulta inicial, avalio seu caso específico e apresento as opções mais adequadas. Cada tratamento é personalizado, por isso é importante conversarmos pessoalmente. Gostaria de agendar uma avaliação?";
    }
    
    if (message.includes("dor") || message.includes("sintoma") || message.includes("problema") || message.includes("doença")) {
      return "Como naturopata, vejo cada sintoma como um sinal do corpo pedindo atenção. A biorressonância é excelente para identificar a raiz dos desequilíbrios. Me conte mais sobre o que você está sentindo - há quanto tempo, intensidade, se há fatores que melhoram ou pioram. Assim posso orientá-lo melhor.";
    }
    
    if (message.includes("contraindicação") || message.includes("risco") || message.includes("seguro") || message.includes("efeito colateral")) {
      return "A biorressonância é um método extremamente seguro e não-invasivo. Não há contraindicações absolutas, mas algumas precauções: gestantes devem informar a gravidez, pessoas com marcapasso precisam de avaliação especial, e durante crises agudas pode ser necessário aguardar estabilização. É sempre importante uma avaliação prévia.";
    }
    
    if (message.includes("tempo") || message.includes("duração") || message.includes("quanto demora") || message.includes("sessões")) {
      return "Cada sessão dura aproximadamente 60-90 minutos. O número de sessões varia conforme o caso - problemas agudos podem necessitar 3-5 sessões, enquanto condições crônicas podem precisar de 8-12 sessões. Na primeira consulta, elaboro um plano personalizado com cronograma específico para seu caso.";
    }
    
    return NATUROPATA_RESPONSES.default[Math.floor(Math.random() * NATUROPATA_RESPONSES.default.length)];
  };

  const handleSendMessage = async () => {
    if (!inputMessage.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputMessage,
      sender: "user",
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage("");
    setIsTyping(true);

    // Simular tempo de resposta do naturopata
    setTimeout(() => {
      const botResponse: Message = {
        id: (Date.now() + 1).toString(),
        text: getResponse(inputMessage),
        sender: "bot",
        timestamp: new Date()
      };

      setMessages(prev => [...prev, botResponse]);
      setIsTyping(false);
    }, 1500 + Math.random() * 1000); // 1.5-2.5 segundos para parecer mais natural
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === "Enter" && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  const quickQuestions = [
    "O que é biorressonância?",
    "Como funciona a nanotecnologia?",
    "Quais são os benefícios?",
    "Como me preparar para o exame?",
    "Quero agendar um exame"
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-green-50 to-blue-50">
      {/* Header */}
      <header className="bg-white shadow-sm">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div className="flex items-center">
              <Link href="/">
                <h1 className="text-2xl font-bold text-gray-900 cursor-pointer">BioResonância</h1>
              </Link>
            </div>
            <nav className="hidden md:flex space-x-8">
              <Link href="/" className="text-gray-500 hover:text-gray-900">Início</Link>
              <Link href="/agendamento" className="text-gray-500 hover:text-gray-900">Agendar</Link>
              <Link href="/chatbot" className="text-green-600 font-medium">Chatbot</Link>
              <Link href="/admin" className="text-gray-500 hover:text-gray-900">Admin</Link>
            </nav>
          </div>
        </div>
      </header>

      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="text-center mb-6">
          <h2 className="text-3xl font-bold text-gray-900">Converse com o Dr. Silva</h2>
          <p className="mt-2 text-lg text-gray-600">
            Naturopata Sênior especializado em Biorressonância
          </p>
        </div>

        <Card className="h-[600px] flex flex-col">
          <CardHeader className="bg-gradient-to-r from-green-600 to-blue-600 text-white rounded-t-lg">
            <CardTitle className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-white rounded-full flex items-center justify-center">
                <span className="text-green-600 font-bold text-lg">Dr</span>
              </div>
              <div>
                <div className="font-semibold">Dr. Silva - Naturopata Sênior</div>
                <div className="text-sm opacity-90">Especialista em Biorressonância • Online</div>
              </div>
            </CardTitle>
          </CardHeader>

          <CardContent className="flex-1 flex flex-col p-0">
            <ScrollArea className="flex-1 p-4" ref={scrollAreaRef}>
              <div className="space-y-4">
                {messages.map((message) => (
                  <div
                    key={message.id}
                    className={`flex ${message.sender === "user" ? "justify-end" : "justify-start"}`}
                  >
                    <div
                      className={`max-w-[80%] p-3 rounded-lg ${
                        message.sender === "user"
                          ? "bg-blue-600 text-white"
                          : "bg-gray-100 text-gray-900"
                      }`}
                    >
                      <p className="whitespace-pre-wrap">{message.text}</p>
                      <p className={`text-xs mt-1 ${
                        message.sender === "user" ? "text-blue-100" : "text-gray-500"
                      }`}>
                        {message.timestamp.toLocaleTimeString("pt-BR", {
                          hour: "2-digit",
                          minute: "2-digit"
                        })}
                      </p>
                    </div>
                  </div>
                ))}
                
                {isTyping && (
                  <div className="flex justify-start">
                    <div className="bg-gray-100 text-gray-900 p-3 rounded-lg">
                      <div className="flex space-x-1">
                        <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                        <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: "0.1s" }}></div>
                        <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: "0.2s" }}></div>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </ScrollArea>

            {/* Perguntas Rápidas */}
            <div className="p-4 border-t bg-gray-50">
              <p className="text-sm text-gray-600 mb-2">Perguntas frequentes:</p>
              <div className="flex flex-wrap gap-2">
                {quickQuestions.map((question, index) => (
                  <Button
                    key={index}
                    variant="outline"
                    size="sm"
                    onClick={() => setInputMessage(question)}
                    className="text-xs"
                  >
                    {question}
                  </Button>
                ))}
              </div>
            </div>

            {/* Input de Mensagem */}
            <div className="p-4 border-t">
              <div className="flex space-x-2">
                <Input
                  value={inputMessage}
                  onChange={(e) => setInputMessage(e.target.value)}
                  onKeyPress={handleKeyPress}
                  placeholder="Digite sua pergunta sobre biorressonância..."
                  className="flex-1"
                  disabled={isTyping}
                />
                <Button 
                  onClick={handleSendMessage} 
                  disabled={!inputMessage.trim() || isTyping}
                  className="bg-green-600 hover:bg-green-700"
                >
                  Enviar
                </Button>
              </div>
              <div className="mt-2 flex justify-between text-xs text-gray-500">
                <span>Pressione Enter para enviar</span>
                <Link href="/agendamento" className="text-blue-600 hover:underline">
                  Agendar diretamente →
                </Link>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
